<template>
  <div>
    <!--  -->
    <div v-if="canVisit === 1">
      <!--  -->
      <h2></h2>
      <el-form :inline="true" :model="addRoleForm" ref="addRoleForm" class="add-form">
        <el-form-item label="" required>
          <el-input v-model="addRoleForm.role_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addRole('addRoleForm')"></el-button>
        </el-form-item>
      </el-form>
      <!--  -->
      <h2></h2>
      <el-table
        :data="roleData"
        class="main-table">
        <el-table-column
          prop="role_id"
          label="ID">
        </el-table-column>
        <el-table-column
          prop="role_name"
          label="">
        </el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-button v-if="scope.row.role_id !== 1" size="small" type="danger" @click="delRole(scope.$index, scope.row)"></el-button>
            <el-button size="small" type="primary" @click="editDialog(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- / dialog -->
      <el-dialog
        title=""
        :visible.sync="dialogVisible">
        <!--  -->
        <el-form ref="accessForm">
          <el-checkbox-group v-model="checkboxArr">
            <el-checkbox
              class="label"
              v-for="access in accessData"
              :key="access.id"
              :label="access.access_id"
              name="access"
              @change="updateRoleAccess">ID:{{ access.access_id }}{{ access.access_url }}<span style="float: right;">{{ access.access_title }}</span></el-checkbox>
          </el-checkbox-group>
        </el-form>
      </el-dialog>
    </div>
    <!--  -->
    <div v-else-if="canVisit === 0"></div>
    <!--  -->
    <div v-else></div>
  </div>
</template>

<script>
import { getRole, addRole, delRole, getAccess, getRoleAccess, addRoleAccess, delRoleAccess } from '../api'
export default {
  name: 'role',
  data () {
    return {
      roleData: [],
      addRoleForm: {
        role_name: ''
      },
      accessData: [],
      checkboxArr: [],
      dialogVisible: false,
      tempRow: {},
      canVisit: -1
    }
  },
  methods: {
    // 
    getRole () {
      getRole()
      .then((data) => {
        data = data.data
        if (data.code !== 1) {
          this.errorMsg(data.message)
          return
        }
        this.roleData = data.data
      })
      .catch((err) => {
        this.errorMsg(err)
      })
    },
    // 
    addRole (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return
        }
        // 
        addRole({ role_name: this.addRoleForm.role_name })
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            this.errorMsg(data.message)
            return
          }
          this.successMsg(data.message)
          this.getRole()
        })
        .catch((err) => {
          this.errorMsg(err)
        })
      })
    },
    // 
    delRole (i, row) {
      delRole({ role_id: row.role_id })
      .then((data) => {
        data = data.data
        if (data.code !== 1) {
          this.errorMsg(data.message)
          return
        }
        this.successMsg(data.message)
        // 
        this.roleData.splice(i, 1)
      })
      .catch((err) => {
        this.errorMsg(err)
      })
    },
    // dialog
    editDialog (i, row) {
      const that = this
      this.dialogVisible = true
      this.checkboxArr = []
      this.tempRow = row
      // 
      new Promise((resolve, reject) => {
        getAccess()
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            that.errorMsg(data.message)
            reject(data.message)
            return
          }
          that.accessData = data.data
          resolve()
        })
        .catch((err) => {
          that.errorMsg(err)
          reject(err)
        })
      })
      // 
      .then((data) => {
        getRoleAccess({ role_id: row.role_id })
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            that.errorMsg(data.message)
            return
          }
          data.data.forEach((item) => {
            that.checkboxArr.push(item.access_id)
          })
        })
        .catch((err) => {
          that.errorMsg(err)
        })
      })
      .catch((err) => {
        that.errorMsg(err)
      })
    },
    // 
    updateRoleAccess (e) {
      const targetEl = e.srcElement
      // 
      if (targetEl.checked) {
        addRoleAccess({ role_id: this.tempRow.role_id, access_id: targetEl.value })
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            this.errorMsg(data.message)
            targetEl.checked = false
            return
          }
          this.successMsg(data.message)
        })
        .catch((err) => {
          this.errorMsg(err)
        })
      } else {
      // 
        delRoleAccess({ role_id: this.tempRow.role_id, access_id: targetEl.value })
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            this.errorMsg(data.message)
            targetEl.checked = true
            return
          }
          this.successMsg(data.message)
        })
        .catch((err) => {
          this.errorMsg(err)
        })
      }
    },
    // 
    successMsg (msg) {
      this.$message({
        message: msg,
        type: 'success'
      })
    },
    // 
    errorMsg (msg) {
      this.$message({
        message: msg,
        type: 'error'
      })
    }
  },
  created () {
    this
    .checkAuth('/admin/role')
    .then((code) => {
      // 
      if (code === 1) {
        this.getRole()
        this.canVisit = 1
      } else if (code === -1) {
      // 
        window.location.href = '/'
      } else if (code === 0) {
      // 
        this.canVisit = 0
      }
    })
    .catch((err) => {
      this.errorMsg(err)
    })
  }
}
</script>

<style>
/* label{
  display: block;
  padding: 4px 0 2px 0px;
  border-bottom: 1px solid #eef1f6;
}
label:hover{
  background: #eef1f6;
}
.el-checkbox{
  margin-left: 0;
}
.el-dialog__body{
  max-height: 460px;
  overflow: auto;
} */
</style>



