<template>
  <div>
    <!--  -->
    <div v-if="canVisit === 1">
      <!--  -->
      <h2></h2>
      <el-form :inline="true" :model="addUserForm" ref="addUserForm" class="add-form">
        <el-form-item label="" required>
          <el-input v-model="addUserForm.username" placeholder=""></el-input>
        </el-form-item>
        <el-button type="primary" @click="addUser('addUserForm')"></el-button>
        </el-form-item>
        
      </el-form>
      <el-popover
        ref="popover"
        placement="right"
        title="Excel"
        width="800"
        trigger="click">
        <img src="../../../common/img/admin-help.png" alt="" style="width: 800px;">
        <p style="font-weight: bold; margin-top: 10px;">*</p>
      </el-popover>
      <h2>
        <i style="color: #20a0ff; cursor: pointer;" v-popover:popover class="el-icon-information"></i>
      </h2>
      <div class="file-wrap">
        <input type="file" @change="readFile" @drag="readFile">
        <div></div>
      </div>
      <!--  -->
      <h2></h2>
      <el-table
        :data="userData"
        border
        class="main-table">
        <el-table-column
          prop="user_id"
          label="ID">
        </el-table-column>
        <el-table-column
          prop="username"
          label="">
        </el-table-column>
        <el-table-column
          prop="real_name"
          label="">
        </el-table-column>
        <el-table-column
          prop="sex"
          label="">
        </el-table-column>
        <el-table-column
          prop="department_name"
          label="">
        </el-table-column>
        <el-table-column
          prop="class_name"
          label="">
        </el-table-column>
        <el-table-column
          prop="student_num"
          label="">
        </el-table-column>
        <el-table-column
          prop="phone_num"
          label="">
        </el-table-column>
        <el-table-column
          prop="qq_num"
          label="QQ">
        </el-table-column>
        <el-table-column label="" width="200">
          <template scope="scope">
            <el-button size="small" @click="editDialog(scope.$index, scope.row)"></el-button>
            <el-button size="small" @click="chooseRoleDialog(scope.$index, scope.row)"></el-button>
            <el-button size="small" type="danger" @click="delUser(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- dialog -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible">
        <!--  -->
        <el-form :model="editUserForm" ref="editUserForm" label-position="right" class="dialog-form">
          <el-form-item label="" required>
            <el-input v-model="editUserForm.username" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" required>
            <el-input v-model="editUserForm.real_name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" required>
            <el-select v-model="editUserForm.sex">
              <el-option
                v-for="sex in sexArr"
                :key="sex.id"
                :label="sex.label"
                :value="sex.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" required>
            <el-select v-model="editUserForm.department_name">
              <el-option
                v-for="department in departmentArr"
                :key="department.id"
                :label="department.department_name"
                :value="department.department_name">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" required>
            <el-input v-model="editUserForm.class_name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" required>
            <el-input v-model="editUserForm.phone_num" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="QQ" required>
            <el-input v-model="editUserForm.qq_num" placeholder="QQ"></el-input>
          </el-form-item>
        </el-form>
        <!-- dialog footer -->
        <span slot="footer">
          <el-button @click="editDialogVisible = false"></el-button>
          <el-button type="primary" @click="updateUser"></el-button>
        </span>
      </el-dialog>

      <!-- /  -->
      <el-dialog
        title=""
        :visible.sync="roleDialogVisible">
        <!--  -->
        <el-form ref="accessForm">
          <el-checkbox-group v-model="checkboxArr">
            <el-checkbox
              class="label"
              v-for="role in roleData"
              :key="role.id"
              :label="role.role_id"
              name="role"
              @change="updateUserRole">ID:{{ role.role_id }}{{ role.role_name }}</el-checkbox>
          </el-checkbox-group>
        </el-form>
      </el-dialog>
    </div>
    <div v-else-if="canVisit === 0"></div>
    <div v-else></div>
  </div>
</template>

<script>
import { getUser, addUser, delUser, getDepartment, updateUser, getRole, getUserRole, addUserRole, delUserRole, addUsers } from '../api'
import XLSX from 'xlsx'
export default {
  name: 'user',
  data () {
    return {
      userData: [],
      addUserForm: {
        username: ''
      },
      editUserForm: {
        username: '',
        real_name: '',
        sex: 0,
        department_name: '',
        class_name: '',
        student_id: '',
        phone_num: '',
        qq_num: ''
      },
      editDialogVisible: false,
      roleDialogVisible: false,
      editIndex: -1,
      canVisit: -1,
      departmentArr: [],
      sexArr: [],
      checkboxArr: [],
      roleData: [], // 
      tempRow: {}
    }
  },
  methods: {
    // 
    getAddUsersParams (arr) {
      const datas = []
      for (let i = 0, len = arr.length; i < len; i++) {
        const obj = {
          username: arr[i][0],
          real_name: arr[i][1],
          student_num: arr[i][2],
          sex: arr[i][3],
          class_name: arr[i][4],
          department_name: arr[i][5],
          phone_num: arr[i][6],
          qq_num: arr[i][7]
        }
        datas.push(obj)
      }
      if (datas[0].username !== '' || datas[0].real_name !== '' || datas[0].student_num !== '' || datas[0].sex !== '' || datas[0].class_name !== '' || datas[0].department_name !== '' || datas[0].phone_num !== '' || datas[0].qq_num !== 'qq') {
        this.errorMsg('Excel')
        return false
      }
      datas.shift()
      return datas
    },
    // 
    readFile (e) {
      const that = this
      const rABS = true
      const files = e.target.files
      const f = files[0]
      const reader = new FileReader()
      reader.onload = function (e) {
        let data = e.target.result
        if (!rABS) {
          data = new Uint8Array(data)
        }
        const workbook = XLSX.read(data, {type: rABS ? 'binary' : 'array'})
        /* DO SOMETHING WITH workbook HERE */
        const firstWorksheet = workbook.Sheets[workbook.SheetNames[0]]
        const arr = XLSX.utils.sheet_to_json(firstWorksheet, { header: 1 })
        arr.length = arr.length
        console.log(arr)
        const datas = that.getAddUsersParams(arr)
        if (!datas) {
          return
        }
        const params = {
          userObjArr: datas
        }
        addUsers(params)
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            that.getUser()
            that.$notify({
              message: data.message,
              type: 'warning',
              duration: 0
            })
            return
          }
          that.successMsg('')
          that.getUser()
        })
        .catch((e) => {
          console.error(e)
        })
      }
      if (rABS) {
        reader.readAsBinaryString(f)
      } else {
        reader.readAsArrayBuffer(f)
      }
    },
    // 
    getUser () {
      getUser()
      .then((data) => {
        data = data.data
        if (data.code !== 1) {
          this.errorMsg(data.message)
          return
        }
        // uid1
        data.data.forEach((data, i, arr) => {
          if (data.user_id === 1) {
            arr.splice(i, 1)
          }
        })
        this.userData = data.data
      })
      .catch((err) => {
        this.errorMsg(err)
      })
    },
    // 
    addUser (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return
        }
        // 
        addUser({ username: this.addUserForm.username })
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            this.errorMsg(data.message)
            return
          }
          this.successMsg(data.message)
          this.getUser()
        })
        .catch((err) => {
          this.errorMsg(err)
        })
      })
    },
    // user_roleuser_id
    delUser (i, row) {
      delUser({ user_id: row.user_id })
      .then((data) => {
        data = data.data
        if (data.code !== 1) {
          this.errorMsg(data.message)
          return
        }
        this.successMsg(data.message)
        // 
        this.userData.splice(i, 1)
      })
      .catch((err) => {
        this.errorMsg(err)
      })
    },
    // dialog
    editDialog (i, row) {
      this.editUserForm = row
      this.sexArr = [
        { value: 0, label: '' },
        { value: 1, label: '' },
        { value: 2, label: '' }
      ]
      // 
      getDepartment()
      .then((data) => {
        data = data.data
        if (data.code !== 1) {
          this.errorMsg(data.message)
          return
        }
        this.departmentArr = data.data
        this.editDialogVisible = true
      })
      .catch((err) => {
        this.errorMsg(err)
      })
    },
    // 
    updateUser () {
      updateUser(this.editUserForm)
      .then((data) => {
        data = data.data
        if (data.code !== 1) {
          this.errorMsg(data.message)
          return
        }
        this.successMsg(data.message)
        this.editDialogVisible = false
      })
      .catch((err) => {
        this.errorMsg(err)
      })
    },
    // dialog
    chooseRoleDialog (i, row) {
      const that = this
      this.roleDialogVisible = true
      this.checkboxArr = []
      this.tempRow = row
      new Promise((resolve, reject) => {
        getRole()
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            that.errorMsg(data.message)
            reject(data.message)
          }
          data.data.forEach((data, i, arr) => {
            if (data.role_id === 1) {
              arr.splice(i, 1)
            }
          })
          that.roleData = data.data
          resolve()
        })
        .catch((err) => {
          that.errorMsg(err)
          reject
        })
      })
      // 
      .then((data) => {
        getUserRole({ user_id: row.user_id })
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            that.errorMsg(data.message)
            return
          }
          data.data.forEach((item) => {
            that.checkboxArr.push(item.role_id)
          })
        })
      })
      .catch((err) => {
        this.errorMsg(err)
      })
      // this.chooseRoleDialog = true
    },
    // 
    updateUserRole (e) {
      const targetEl = e.srcElement
      // 
      if (targetEl.checked) {
        addUserRole({ user_id: this.tempRow.user_id, role_id: targetEl.value })
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            this.errorMsg(data.message)
            targetEl.checked = false
            return
          }
          this.successMsg(data.message)
        })
        .catch((err) => {
          this.errorMsg(err)
        })
      } else {
      // 
        delUserRole({ user_id: this.tempRow.user_id, role_id: targetEl.value })
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            this.errorMsg(data.message)
            targetEl.checked = true
            return
          }
          this.successMsg(data.message)
        })
        .catch((err) => {
          this.errorMsg(err)
        })
      }
    },
    // 
    successMsg (msg) {
      this.$message({
        message: msg,
        type: 'success'
      })
    },
    // 
    errorMsg (msg) {
      this.$message({
        message: msg,
        type: 'error'
      })
    }
  },
  created () {
    this
    .checkAuth('/admin/user')
    .then((code) => {
      // 
      if (code === 1) {
        this.getUser()
        this.canVisit = 1
      } else if (code === -1) {
      // 
        window.location.href = '/'
      } else if (code === 0) {
      // 
        this.canVisit = 0
      }
    })
    .catch((err) => {
      this.errorMsg(err)
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.file-wrap {
  position: relative;
  height: 120px;
  overflow: hidden;
}
.file-wrap input {
  width: 420px;
  height: 120px;
  line-height: 120px;
  border: 2px dashed #dddee1;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  cursor: pointer;
  z-index: 1;
}
.file-wrap div {
  width: 420px;
  height: 116px;
  line-height: 120px;
  text-align: center;
  border: 2px dashed #dddee1;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  cursor: pointer;
}
</style>



