<template>
  <div>
    <!--  -->
    <div v-if="canVisit === 1">
      <!--  -->
      <h2></h2>
      <el-form :inline="true" :model="addAccessForm" ref="addAccessForm" class="add-form">
        <el-form-item label="" required>
          <el-input v-model="addAccessForm.title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="URL" required>
          <el-input v-model="addAccessForm.url" placeholder=""></el-input>
        </el-form-item><el-form-item>
        <el-button type="primary" @click="addAccess('addAccessForm')"></el-button>
        </el-form-item>
      </el-form>
      <!--  -->
      <h2></h2>
      <el-table
        :data="accessData"
        class="main-table"
        height="450">
        <el-table-column
          prop="access_id"
          label="ID">
        </el-table-column>
        <el-table-column
          prop="access_title"
          label="">
        </el-table-column>
        <el-table-column
          prop="access_url"
          label="">
        </el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-button size="small" @click="editDialog(scope.$index, scope.row)"></el-button>
            <el-button size="small" type="danger" @click="delAccess(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- dialog -->
      <el-dialog
        title=""
        :visible.sync="dialogVisible">
        <!--  -->
        <el-form :inline="true" :model="editAccessForm" ref="editAccessForm">
          <el-form-item label="" required>
            <el-input v-model="editAccessForm.access_title" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="URL" required>
            <el-input v-model="editAccessForm.access_url" placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <!-- dialog footer -->
        <span slot="footer">
          <el-button @click="dialogVisible = false"></el-button>
          <el-button type="primary" @click="updateAccess"></el-button>
        </span>
      </el-dialog>
    </div>
    <div v-else-if="canVisit === 0"></div>
    <div v-else></div>
  </div>
</template>

<script>
import { getAccess, addAccess, delAccess, updateAccess } from '../api'
export default {
  name: 'access',
  data () {
    return {
      accessData: [],
      addAccessForm: {
        title: '',
        url: ''
      },
      editAccessForm: {
        access_id: -1,
        access_title: '',
        access_url: ''
      },
      dialogVisible: false,
      editIndex: -1,
      canVisit: -1
    }
  },
  methods: {
    // 
    getAccess () {
      getAccess()
      .then((data) => {
        data = data.data
        if (data.code !== 1) {
          this.errorMsg(data.message)
          return
        }
        this.accessData = data.data
      })
      .catch((err) => {
        this.errorMsg(err)
      })
    },
    // 
    addAccess (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return
        }
        // 
        addAccess({
          access_title: this.addAccessForm.title,
          access_url: this.addAccessForm.url
        })
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            this.errorMsg(data.message)
            return
          }
          this.successMsg(data.message)
          this.getAccess()
        })
        .catch((err) => {
          this.errorMsg(err)
        })
      })
    },
    // role_accessaccess_id
    delAccess (i, row) {
      delAccess({ access_id: row.access_id })
      .then((data) => {
        data = data.data
        if (data.code !== 1) {
          this.errorMsg(data.message)
          return
        }
        this.successMsg(data.message)
        // 
        this.accessData.splice(i, 1)
      })
      .catch((err) => {
        this.errorMsg(err)
      })
    },
    // dialog
    editDialog (i, row) {
      this.editAccessForm = row
      this.dialogVisible = true
      this.editIndex = i
    },
    // 
    updateAccess () {
      updateAccess(this.editAccessForm)
      .then((data) => {
        data = data.data
        this.dialogVisible = false
        if (data.code !== 1) {
          this.errorMsg(data.message)
          return
        }
        // 
        this.successMsg(data.message)
      })
      .catch((err) => {
        this.errorMsg(err)
      })
    },
    // 
    successMsg (msg) {
      this.$message({
        message: msg,
        type: 'success'
      })
    },
    // 
    errorMsg (msg) {
      this.$message({
        message: msg,
        type: 'error'
      })
    }
  },
  created () {
    this
    .checkAuth('/admin/access')
    .then((code) => {
      // 
      if (code === 1) {
        this.getAccess()
        this.canVisit = 1
      } else if (code === -1) {
      // 
        window.location.href = '/'
      } else if (code === 0) {
      // 
        this.canVisit = 0
      }
    })
    .catch((err) => {
      this.errorMsg(err)
    })
  }
}
</script>

<style scoped>
</style>




