<template>
  <div>
    <!--  -->
    <div v-if="canVisit === 1">
      <!--  -->
      <h2></h2>
      <el-form :inline="true" :model="addDepartmentForm" ref="addDepartmentForm" class="add-form">
        <el-form-item label="" required>
          <el-input v-model="addDepartmentForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-button type="primary" @click="addDepartment('addDepartmentForm')"></el-button>
        </el-form-item>
      </el-form>
      <!--  -->
      <h2></h2>
      <el-table
        :data="departmentData"
        class="main-table">
        <el-table-column
          prop="department_id"
          label="ID">
        </el-table-column>
        <el-table-column
          prop="department_name"
          label="">
        </el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-button size="small" type="danger" @click="delDepartment(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div v-else-if="canVisit === 0"></div>
    <div v-else></div>
  </div>
</template>

<script>
import { getDepartment, addDepartment, delDepartment } from '../api'
export default {
  name: 'department',
  data () {
    return {
      departmentData: [],
      addDepartmentForm: {
        name: ''
      },
      canVisit: -1
    }
  },
  methods: {
    // 
    getDepartment () {
      getDepartment()
      .then((data) => {
        data = data.data
        if (data.code !== 1) {
          this.errorMsg(data.message)
          return
        }
        this.departmentData = data.data
      })
      .catch((err) => {
        this.errorMsg(err)
      })
    },
    // 
    addDepartment (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return
        }
        // 
        addDepartment({ department_name: this.addDepartmentForm.name })
        .then((data) => {
          data = data.data
          if (data.code !== 1) {
            this.errorMsg(data.message)
            return
          }
          this.successMsg(data.message)
          this.getDepartment()
        })
        .catch((err) => {
          this.errorMsg(err)
        })
      })
    },
    // 
    delDepartment (i, row) {
      delDepartment({ department_id: row.department_id })
      .then((data) => {
        data = data.data
        if (data.code !== 1) {
          this.errorMsg(data.message)
          return
        }
        this.successMsg(data.message)
        // 
        this.departmentData.splice(i, 1)
      })
      .catch((err) => {
        this.errorMsg(err)
      })
    },
    // 
    successMsg (msg) {
      this.$message({
        message: msg,
        type: 'success'
      })
    },
    // 
    errorMsg (msg) {
      this.$message({
        message: msg,
        type: 'error'
      })
    }
  },
  created () {
    this
    .checkAuth('/admin/department')
    .then((code) => {
      // 
      if (code === 1) {
        this.getDepartment()
        this.canVisit = 1
      } else if (code === -1) {
      // 
        window.location.href = '/'
      } else if (code === 0) {
      // 
        this.canVisit = 0
      }
    })
    .catch((err) => {
      this.errorMsg(err)
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



