import axios from 'axios'
import qs from 'qs'
import Vue from 'vue'
// baseURLcookie
const ENV = 'dev'
if (ENV === 'dev') {
  axios.defaults.baseURL = 'http://localhost:3000'
  axios.defaults.withCredentials = true // cookiehttps://developer.mozilla.org/zh-CN/docs/Web/API/XMLHttpRequest/withCredentials
}
// auth
Vue.prototype.checkAuth = (webUrl, cb) => {
  return new Promise((resolve, reject) => {
    axios
    .get(webUrl)
    .then((data) => {
      data = data.data
      resolve(data.code)
    })
    .catch((err) => {
      reject(err)
    })
  })
}
